import moment from "moment-timezone"
import { codeEnArr, codeKrArr } from "./conditionCode"

export const fnGetWeatherData = (latlonObj) => {
    return new Promise((resolve) => {
        const { lat, lon } = latlonObj
        let url = `https://api.openweathermap.org/data/3.0/onecall?lat=${lat}&lon=${lon}&appid=${process.env.REACT_APP_OPEN_WEATHER_KEY}`
        fetch(url) //외부파일 받아옴(비동기)
            .then((data) => { //오류 안 났을 때
                resolve(data.json())//스크립트 형태로 바꾸면서 다음 함수 실행하게 해줌
            })
            .catch(() => { //오류 났을 때 ... catch가 자체적으로 에러를 내줍니다...reject 안 써도 됨
                alert('날씨 정보를 받아오던 중 오류가 발생했습니다.')
            })
    })
}

/* 날씨 정보를 가공해서 리턴하는 함수 */
export const fnSetWeatherInfo = (weatherDataObj, timeZone) => {


    let sunrise; let sunset;
    if (weatherDataObj.sunrise) {
        //1000 곱해서 자바스크립트 타임스탬프로 만들어주기 
        sunrise = moment(weatherDataObj.sunrise * 1000).tz(timeZone).format(`A hh:mm`)
        sunset = moment(weatherDataObj.sunset * 1000).tz(timeZone).format(`A hh:mm`)
    } else {
        sunrise = sunset = '-- : -- : --'
    }
    let mmdd = moment(weatherDataObj.dt * 1000).tz(timeZone).format(`M월 D일`)
    let currentTime = moment(weatherDataObj.dt * 1000).tz(timeZone).format(`hh:mm`)
    let hhmm = moment(weatherDataObj.dt * 1000).tz(timeZone).format(`HH:mm`)
    let apm = moment(weatherDataObj.dt * 1000).tz(timeZone).format('A')
    let dt = moment(weatherDataObj.dt * 1000).tz(timeZone).format(`yyyy년 M월 D일`)
    let dayArr = ['일', '월', '화', '수', '목', '금', '토']
    let day = dayArr[moment(weatherDataObj.dt * 1000).tz(timeZone).day()] //배열 숫자가나옴
    //weatherDataObj.sunrise = 초(밀리세컨드 단위)

    //최저온도 최고온도
    let temp; let tempMin; let tempMax; let tempMorn; let tempDay; let tempNight
    //객체입니다
    if (typeof (weatherDataObj.temp) === 'object') {//daily도 객체
        tempMax = (weatherDataObj.temp.max - 273.15).toFixed(1)
        tempMin = (weatherDataObj.temp.min - 273.15).toFixed(1)
        tempDay = (weatherDataObj.temp.day - 273.15).toFixed(1)
        tempNight = (weatherDataObj.temp.night - 273.15).toFixed(1)
        tempMorn = (weatherDataObj.temp.morn - 273.15).toFixed(1)

    } else {//number
        temp = (weatherDataObj.temp - 273.15).toFixed(1)
    }

    let icon = weatherDataObj.weather[0].icon
    let bg = weatherDataObj.weather[0].main
    let desc = weatherDataObj.weather[0].description
    desc = codeKrArr[codeEnArr.indexOf(desc)]
    let windDeg = weatherDataObj.wind_deg
    let windSpeed = weatherDataObj.wind_speed
    let rain = (weatherDataObj.rain)
    ? (typeof(weatherDataObj.rain) ==='object')
    //rain이 있는데 걔가 object(객체)이면 
        ? weatherDataObj.rain['1h']
        //이걸 리턴해주고
        :
        weatherDataObj.rain
        //아니라면 그냥 오브젝트 레인을 넣어
    :
    0
    let snow = (weatherDataObj.snow)
    ? (typeof(weatherDataObj.snow) ==='object')
    //snow이 있는데 걔가 object(객체)이면 
        ? weatherDataObj.snow['1h']
        //이걸 리턴해주고
        :
        weatherDataObj.snow
        //아니라면 그냥 오브젝트 레인을 넣어
    :
    0


    let humidity = weatherDataObj.humidity
    let uv = weatherDataObj.uvi
    let uviDesc
    if (uv <= 3){ uviDesc = '낮음'}
    else if (uv >= 3 && uv <= 6){ uviDesc = '보통'}
    else if (uv > 6 && uv <= 8){ uviDesc = '높음'}
    else if (uv > 8 && uv <= 11){ uviDesc = '매우높음'}
    else{ uviDesc = '위험'}

    let feelsLike
    if(typeof(weatherDataObj.feels_like) === 'object'){
        feelsLike = (weatherDataObj.feels_like.day - 273.15).toFixed(1)
    }else{
        feelsLike = weatherDataObj.feels_like
    }

    let clouds = weatherDataObj.clouds
    let pressure = weatherDataObj.pressure



    return {
        temp, icon, bg, desc,
        windDeg, windSpeed, humidity, rain, uv, uviDesc,
        sunrise, sunset, currentTime, dt, day, apm, mmdd,
        temp, tempMin, tempMax, tempMorn, tempDay, tempNight,
        snow, feelsLike, clouds, pressure, hhmm,
    }
}//fnSetWeatherInfo