import React, { useContext, useLayoutEffect, useState } from 'react';
import { AppContext } from '../../App';
import { fnGetAddress, fnInitMap } from '../../js/map';
import CompLoader from '../compLoader/CompLoader';
import { fnGetWeatherData } from '../../js/weather';

const CopmMap = () => {
    const [_isActive, _setIsActive] = useState(false)
    const { _setWeatherData,
        _address,
        _setTimezone, _setAddressEn, _setAddressKo,
    } = useContext(AppContext)
    /* 
        컴포넌트가 만들어지는(실행되는) 순서 : 부모->자식
        컴포넌트가 완료(화면에 출력)되는 시점(useEffect) : 자식(위경도를호출)->부모(위경도를갱신) (useEffect나 LayoutEffect 실행 시점)
    
    */
    const {
        _latLon, _setLatLon, //위경도
    } = useContext(AppContext)
    //다짜고짜 map출력하니까 null값이 들어감



    //fnInitMap의 파라미터로 전달되어서 클릭이벤트 내에서 호출될 함수
    const fnMapClickHandler = async (latLonObj) => {
        //날씨 데이터 비동기로 받아오기
        let addressEn = await fnGetAddress(latLonObj, 'en') //주소 반환 받아오기
        _setAddressEn(addressEn)
        let addressKo = await fnGetAddress(latLonObj, 'ko') //주소 반환 받아오기
        _setAddressKo(addressKo)

        const weatherData = await fnGetWeatherData(latLonObj) //api비동기 통신으로 날씨정보를 반환
        _setWeatherData(weatherData)
        _setIsActive(false)
        document.querySelector(`.app-inner`).scrollTo({ 'top': 0, 'behavior': 'smooth' })
        document.querySelectorAll(`.section-inner`).forEach((el) => {
            el.scrollTo({ 'top': 0, 'left': 0, behavior:'smooth'})
        })
    }//fnMapClickHandler 컬링...




    //div가 컴퍼넌트가 아니었으면 useEffect안해도됨
    useLayoutEffect(() => {
        (_latLon) && fnInitMap(_latLon, fnMapClickHandler) //구글맵이 출력되고...?
    }, [_latLon]) //맵이 만들어지고...?
    //_latLon 가 있을 때만 div 출력 ?...

    return (
        <section className={`comp-map ${_isActive && 'active'}`}>
            <div className="btns">
                <button onClick={() => { _setIsActive(true) }}><i className="fa-solid fa-earth-americas"></i></button>
                <button onClick={() => {
                    _setIsActive(false)
                    }}><i className="fa-solid fa-xmark"></i></button>
        </div>{/* btns */ }
    <div className="section-inner">

        {
            (_latLon) //위경도가 출력되면
                ?
                <div id="map"></div>
                :
                <CompLoader />
        }
    </div>
        </section >
    );
};

export default CopmMap;