import React, { useContext } from 'react';
import { fnSetWeatherInfo } from '../../js/weather';
import { AppContext } from '../../App';
import $ from 'jquery';

const CompDailyLi = ({ data }) => {
    const { _weatherData } = useContext(AppContext)


    //출력용으로 가공해주는 함수(어떤객체?)
    const { //fnSetWeatherInfo destructuring
        mmdd, day, desc, icon,
        temp, tempMin, tempMax, tempMorn, tempDay, tempNight,
        rain, snow, feelsLike,
        clouds, humidity, pressure, uv, uviDesc, windDeg, windSpeed,
    } = fnSetWeatherInfo(data, _weatherData.timezone)
    
    const fnAccordion = function(e){
        e.preventDefault()
        $('.comp-daily li .detail').stop().slideUp()
        $(e.currentTarget).siblings('.detail').stop().slideToggle()
        $('.comp-daily li a').not($(e.currentTarget)).removeClass('active')
        $(e.currentTarget).toggleClass('active')
    }

    return (
        <li>
            <a href='#' onClick={fnAccordion}>
                <p className='date'>
                    <span><i className="fa-regular fa-calendar-days"></i> {mmdd} ({day})</span> <span className='desc'>- {desc}</span>
                </p>
                <p className='info'>
                    <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
                    <span className='temp'>
                        <em><i className="fa-solid fa-temperature-arrow-down"></i> {tempMin} &#176;c | <i className="fa-solid fa-temperature-arrow-up"></i> {tempMax}&#176;c</em>
                    </span>
                    {
                        (rain !== 0) && //이 조건이 맞으면 아래 내용 출력
                        <span>
                            <i className="fa-solid fa-umbrella"></i>
                            <em>{rain}mm</em>
                        </span>
                    }
                    {
                        (snow !== 0) &&
                        <span>
                            <i className="fa-solid fa-snowflake"></i>
                            <em>{snow}mm</em>
                        </span>
                    }
                </p>
                <i className="fa-solid fa-caret-down"></i>
            </a>
            <div className='detail'>
                <p>
                    <span>
                        <i className="fa-solid fa-person"></i> <i className="fa-solid fa-temperature-half"></i>
                        <b>체감온도</b>
                        <em>{feelsLike}&#176;c</em>
                    </span>
                </p>

                <p>
                    <span>
                        <i className="fa-solid fa-location-arrow"></i>
                        <b>풍향</b>
                        <em>{windDeg}<small>&#176;</small></em>
                    </span>
                    <span>
                        <i className="fa-solid fa-wind"></i>
                        <b>풍속</b>
                        <em>{windSpeed}<small>m/s</small></em>
                    </span>
                </p>
                <p>
                    <span>
                        <i className="fa-solid fa-cloud"></i>
                        <b>구름양</b>
                        <em>{clouds}<small>%</small></em>
                    </span>
                </p>

                <p>
                    <span>
                        <i className="fa-solid fa-droplet"></i>
                        <b>습도</b>
                        <em>{humidity}<small>%</small></em>
                    </span>
                </p>

                <p>
                    <span>
                        <i className="fa-solid fa-angles-down"></i>
                        <b>기압</b>
                        <em>{pressure}<small>hPa</small></em>
                    </span>
                </p>

                <p>
                    <span>
                        <i className="fa-solid fa-sun"></i>
                        <b>자외선 수치</b>
                        <em>{uv}, ({uviDesc})</em>
                    </span>
                </p>
            </div>
        </li>
    );
};

export default CompDailyLi;