
//위경도 받아오는 함수, app에 set하기 위해 만든 함수
/* 현재위치 정보를 리턴해주는 함수 */
export const fnGetLatLon = () => {//현재 위치정보를 리턴해줘요
    return new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(
            (location) => {//success
                resolve(
                    {
                        lat: location.coords.latitude, //위도
                        lon: location.coords.longitude //경도
                    })

            },

            (error) => {//failed
                if (error.code === 1) alert('위치 접근 권한을 허용해주세요.')
                else alert('위치 정보 수신 중 오류가 발생했습니다.')
                reject(new Error('위치 정보를 받아올 수 없습니다.'))
                //= throw new Error('에러 발생. 콘솔에 이 글이 출력됩니다.')
            })//getCurrentPosition
    })//Promise
}//fnGetLatLon

