import { createContext, useLayoutEffect, useState } from "react";
import { fnGetAddress, fnGetLatLon } from "./js/map";
import { fnGetWeatherData } from "./js/weather";

import CompCurrent from "./components/compCurrent/CompCurrent";
import CompDaily from "./components/compDaily/CompDaily";
import CompHourly from "./components/compHourly/CompHourly";
import CopmMap from "./components/compMap/CopmMap";
import moment from "moment-timezone";

export const AppContext = createContext()

function App() {
  const [_latLon, _setLatLon] = useState(null) //위경도
  const [_weatherData, _setWeatherData] = useState(null)//날씨데이터
  const [_timezone, _setTimezone] = useState(null)
  const [_addressEn, _setAddressEn] = useState(null)
  const [_addressKo, _setAddressKo] = useState(null)


//이 함수를 실행하려면 await를 해줘야 하는데 components에는 await를 못 해준다
//그래서 async 달아 줄 함수를 새로 만듦
//위경도, 주소, 날씨정보를 초기설정하는 async함수
const fnAppInit = async () => {
    let latLonObj = await fnGetLatLon() //위도와 경도를 객체로 반환(현재 위치)
    _setLatLon(latLonObj)//위경도 상태 설정, 지도 갱신
    let addressKo = await fnGetAddress(latLonObj,'ko') //한글 주소 반환 받아오기
   _setAddressKo(addressKo)
    let addressEn = await fnGetAddress(latLonObj,'en') //영문 주소 반환 받아오기
   _setAddressEn(addressEn)
    //날씨 데이터 비동기로 받아오기
    const weatherData = await fnGetWeatherData(latLonObj) //api비동기 통신으로 날씨정보를 반환
    _setWeatherData(weatherData)

  }//fnAppInit


  useLayoutEffect(() => { //리얼돔이 구축된 후
    fnAppInit()//위경도 상태 설정
  }, [])

  return (
    <AppContext.Provider value={{
      fnAppInit,
      _latLon, _setLatLon, //위경도
      _weatherData, _setWeatherData, //날씨정보
      _addressEn, _setAddressEn, //주소정보 영문
      _addressKo, _setAddressKo,
      _timezone, _setTimezone, //타임존
    }}>
      {/* 1. src require / 2. js 에서 찾기 pocess ? .. . 설명못들음*/}
      <h1><img src={`${process.env.PUBLIC_URL}/img/main/title.png`} alt="" /></h1>
      <div className="app-inner">
        <CompCurrent/>
        <CopmMap />
        <CompHourly />
        <CompDaily />

      </div>
    </AppContext.Provider>
  );
}

export default App;
